import './App.css';
import React, {useEffect, useState} from "react";
import {BrowserRouter, Routes, Route, Link} from "react-router-dom";

import Sidebar from "./element/Sidebar";
import SidebarItem from "./element/SidebarItem";
import MyCalendar from "./pages/MyCalender";
import Chat from "./pages/Chat"
import axios from "axios";


export default function App() {

    // const events = [
    //     {title: ' ', start: '2023-09-01', end:'2023-09-03', color:'red'},
    //     {title: ' ', date: '2023-09-22', color:'#546563'},
    //      {title: ' ', date: '2023-09-24', color:'#456485'},
    //      {title: ' ', date: '2023-09-21', color:'#544864'},
    //      {title: ' ', date: '2023-09-27', color:'#989755'},
    // ];
    const [events, setEvents] = useState([]);

    useEffect(() => {
        //  API    .
        async function fetchEvents() {
            try {
                // Axios  GET  .  API   .
                const response = await axios.get('http://124.54.16.127:8080/api/v1/calendar?year=2023&month=9');

                // API   events  .
                setEvents(response.data);
            } catch (error) {
                //   .        .
                console.error('Error fetching events:', error);
            }
        }

        //    .
        fetchEvents();
    }, []); //             .

    return (
        <div className="App">
            <BrowserRouter>
                <Sidebar>
                </Sidebar>
                <Routes>
                    <Route path="/" exact element={<div className="CalendarContainer"><MyCalendar events={events} /></div>} />
                    <Route path="/chat" element={<div className="ChatContainer"><Chat /></div>} />
                    </Routes>
            </BrowserRouter>


        </div>



    );
}

