import React, {useState} from 'react';
import '../Chat.css';
import axios from 'axios';
import {Link} from "react-router-dom";

function App() {
    const [chatHistory, setChatHistory] = useState([]);
    const [userInput, setUserInput] = useState('');
    const [prompt, setPrompt] = useState('');

    const handleBoxClick = () => {

    };

    // useEffect(() => {
    //    const fetchData = async () => {
    //      try {
    //        const response = await axios.post('http://124.54.16.127:8080//api/v1/chat/completion');
    //        const data = response.data;
    //        setPrompt(data.prompt);
    //        setChatHistory(data.history.map((message) => ({ role: 'user', content: message })));
    //      } catch (error) {
    //        console.error('API    :', error);
    //      }
    //    };
    //
    //    fetchData();
    //  }, []);

    const handleInputChange = (e) => {
        setUserInput(e.target.value);
    };

    const handleSendMessage = async () => {
        if (userInput.trim() === '') return;

        const newUserMessage = {
            role: 'user',
            content: userInput,
            icon: '',
        };
        setChatHistory([...chatHistory, newUserMessage]);
        setUserInput('');
        try {
            const response = await axios.post('http://124.54.16.127:8080/api/v1/chat/completion', {
                prompt: userInput, history: []
            });
            const content = response.data.content;
            const events = response.data.events;
            for (const event of events) {
                const title = event.title;
                const color = event.color;
                const start = event.start;
                const end = event.end;

                const squareBoxStyle = {
                  display: 'inline-block',
                    // padding:15,
                  width: '20px', /*     */
                  height: '30px', /*     */
                  backgroundColor: color || 'event.color', /*       */
                   marginLeft: '20px',
                  verticalAlign: 'middle', /*    */
                };
                const aiResponse = {
                    role: 'assistant',
                    content: (
                        <>
                             <div>{content}</div> {/* content   */}

                            <Link to="/" style={{ textDecoration: "none" }} onClick={handleBoxClick}>
                    <div style={{ display: 'flex', alignItems: 'center' }}>
                        {/* , title, start   div     */}

                            <div style={squareBoxStyle}></div>
                        <div onClick={handleBoxClick}>
                            <div style={{ fontSize: '7px', backgroundColor: '#212936', padding: '8px 12px', width: '360px', color:'white' }}>
                                <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                    <div>{title}</div>
                                    <div>|</div>
                                    <div>{start}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Link>
                        </>
                    ),
                    // events: [title, color, start, end],
                    // events: '${title} | ${start}',
                    icon: '',
                };
                setPrompt('');
                setChatHistory((prevHistory) => [...prevHistory, aiResponse]);
            }
            // events 0    
            if (events.length === 0) {
                const aiResponseWithoutEvents = {
                    role: 'assistant',
                    content: content,
                    icon: '',
                };
                setChatHistory((prevHistory) => [...prevHistory, aiResponseWithoutEvents]);
            }

        } catch (error) {
            console.error('    :', error);
        }


    };

    const handleInputKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleSendMessage();
        }
    };

    return (
        <div className="chat-container">
            <div className="chat-history" id="chat-history">
                <div className="prompt">{prompt}</div>
                {chatHistory.map((message, index) => (
                    <div
                        key={index}
                        className={`message ${message.role === 'user' ? 'user-message' : 'ai-message'}`}
                    >
                        <span className="icon">{message.icon}</span>
                        <div>{message.content}
                        </div>
                    </div>
                ))}
            </div>
            <div className="chat-input">
                <input
                    type="text"
                    id="user-input"
                    placeholder=" ..."
                    value={userInput}
                    onChange={handleInputChange}
                    onKeyPress={handleInputKeyPress}
                />
                <button id="send-button" onClick={handleSendMessage}>
                    
                </button>
            </div>
        </div>
    );
}

export default App;